import axios from 'axios'


//全局axios配置
axios.defaults.timeout = 10000;


function createRequest(url, method = 'get', data = {}){
  if(method === 'get'){
    return axios({
      url,
      method,
      params: data
    })
  }else {
    return axios({
      url,
      method,
      data
    })
  }
}

export default {
  ReportList(){
    return createRequest('/report')
  }
}






