<template>
  <div>
    <Row type="flex" justify="center">
      <Col span="20">
      <Table border :columns="columns" :data="templateList"  :loading="loading"></Table>
      </Col>
    </Row>

  </div>
</template>

<script>
  import api from '@/api/api'
  export default {
    name: 'HelloWorld',
    data () {
      return {
        templateList: [],
        columns: [
          {
            title: '来源',
            render: (h, params) => {
                return h('span', params.row.from.hostIP)
            }
          }, {
            title: '目标',
            render: (h, params) => {
              if(params.row.type === 'Pod')
                return h('span', params.row.to.podIP);

              if(params.row.type === 'Node')
                return h('span', params.row.to.hostIP);

              if(params.row.type === 'Service')
                return h('span', params.row.to.port);


            }
          }, {
            title: '结果',
            render: (h, params) => {
              return h('span', params.row.result)
            }
          }, {
            title: '类型',
            render: (h, params) => {
              return h('span', params.row.type);

            }
          }
        ],
      }
    },
    created(){
      console.log(api);
      api.ReportList().then(response => {
        this.templateList = response.data.items;
      })
    }


  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  * {
    padding: 0;
    margin: 0;
    outline: none;
  }
  html, body {
    height: 100%;
  }

  body {
    font-size: 14px;
    overflow-x: hidden;
  }

  a {
    text-decoration: none;
    cursor: pointer;
    color: #555;
  }
  a:hover {
    text-decoration: none;
  }
  img {
    border: none;
  }
  li {
    list-style: none;
  }

  input {
    outline: none;
  }

</style>



